@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,700,900&display=swap);
:root {
    --gradient: linear-gradient(135deg, #72edf2 10%, #5151e5 100%);
    --bg-color: #343d4b;
    --bg-color--card: #222831;

    --main-text-color: #ffffff;

    --main-border-radius: 25px;
    --secondary-border-radius: 15px;
}

body {
    width: 100%;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    background-color: #343d4b;
    background-color: var(--bg-color);
    color: #ffffff;
    color: var(--main-text-color);
}

.Header_heading__15IwO {
    padding: 2rem;
    text-align: center;
    font-weight: 900;
    font-size: 2rem;
}

.Header_light__BMJAE {
    font-weight: 100;
}

.Form_input__3m8nv {
    border-radius: var(--main-border-radius);
    margin-bottom: 1rem;
}

.Form_button__181bY {
    border: none;
    border-radius: var(--main-border-radius);
    padding: 0.65rem;
    background-image: var(--gradient);
    color: var(--main-text-color);
    font-weight: 700;
    box-shadow: 0 0 30px -5px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    line-height: normal;
    line-height: initial;
    width: 100%;
    height: 38px;
}

.Form_button__181bY:hover {
    color: var(--main-text-color);
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
}

.Error_error__3l6Ps {
    bottom: 0;
}

.Loader_box__2vYzk {
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Loader_box__2vYzk svg path,
.Loader_box__2vYzk svg rect {
    fill: #65b6ee;
}

.CurrentDay_card__2NPYL {
    border-radius: var(--main-border-radius);
}

.CurrentDay_cardInner__nPA9w {
    z-index: 1;
    height: 300px;
}

.CurrentDay_gradient__1mVXU {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: var(--gradient);
    background-size: cover;
    border-radius: var(--main-border-radius);
    opacity: 0.7;
}

.CurrentDay_img__1WzDU {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: url(/weather-app/static/media/mountain.ef3e6e15.jpg);
    background-size: cover;
    border-radius: var(--main-border-radius);
}

.CurrentDay_weekday__3L8Az {
    width: 100px;
}

.UpcomingDaysForecastItem_weekday__1gUox {
    width: 100px;
    border-radius: var(--secondary-border-radius);
}

.UpcomingDaysForecastItem_weekday__1gUox:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    background: #fff;
    color: #222831;
    box-shadow: 0 0 40px -5px rgba(0, 0, 0, 0.2);
}

.UpcomingDaysForecast_weekList__25Tcd {
    list-style-type: none;
    padding: 0;
    box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.25);
    border-radius: var(--secondary-border-radius);
    background-color: var(--bg-color--card);
}

.Forecast_card__3e4ZD {
    border-radius: var(--main-border-radius);
}

.Forecast_box__hHN7z {
    border-radius: var(--main-border-radius);
    box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
    background-color: var(--bg-color--card);
    color: var(--main-text-color);
    width: 85%;
    max-width: 675px;
    min-height: 300px;
}

.Page_box__1v-vW {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    border-radius: var(--main-border-radius);
    box-shadow: 0 0 24px 7px rgba(0, 0, 0, 0.2);
    background-color: var(--bg-color--card);
    color: var(--main-text-color);
    width: 85%;
    max-width: 675px;
    min-height: 300px;
}
.reload,
.refresh {
    display:flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-top: 3rem;
    padding: 1rem;
    border-radius: 8px;
    border-style: none;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    flex-shrink: 0;
    font-family: "Inter UI","SF Pro Display",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;
    font-size: 16px;
    font-weight: 500;
    height: 4rem;
    padding: 0 1.6rem;
    text-align: center;
    text-shadow: rgba(0, 0, 0, 0.25) 0 3px 8px;
    transition: all .5s;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    background-image: linear-gradient(92.88deg, #455EB5 9.16%, #5643CC 43.89%, #673FD7 64.72%);
}

.refresh:hover {
  box-shadow: rgba(80, 63, 205, 0.5) 0 1px 30px;
  transition-duration: .1s;
}

@media (min-width: 768px) {
  .refresh {
    padding: 0 2.6rem;
  }
}
